import axios from 'axios'
import tools from '@/common/tools.js'
import store from '../store'

axios.defaults.baseURL = process.env.API_ROOT
axios.defaults.withCredentials = true

// http request 拦截器
axios.interceptors.request.use(
  config => {
    if (tools.getCookie('token')) { // 判断是否存在token，如果存在的话，则每个http header都加上token
      console.log('token的值是：', store.state.userData.token, `token ${tools.getCookie('token')}`)
      config.headers.Authorization = `${tools.getCookie('token')}`
      console.log('interceptors config=', config)
    }
    return config
  },
  err => {
    return Promise.reject(err)
  })

// http response 拦截器
axios.interceptors.response.use(
  response => {
    console.log('拦截器出来的数据', response)
    return response
  },
  error => {
    if (error.response) {
      switch (error.response.status) {
        case 401:
          // 返回 401 清除token信息并跳转到登录页面
          tools.deleteCookie('token')
          location.reload()
          break
        case 500:
          tools.showError('系统内部错误')
          break
      }
    }
    return Promise.reject(error.response.data) // 返回接口返回的错误信息
  })



