// 全局过滤器
const vfilter = {
  quzheng(value) {
    if (!value) return ''
    /* return Math.floor(value) */
    let f = Math.round(value * 100) / 100
    let s = f.toString()
    return s
  },
  // 日期格式化，参数为yyyy-MM-dd，HH:mm:ss
  dateFormat(date, format) {
    date = typeof date == 'string' ? new Date(date*1000) : new Date(date*1000);
    var _weekName = ['星期日', '星期一', '星期二', '星期三', '星期四', '星期五', '星期六'];
    var _monthName = ['一月', '二月', '三月', '四月', '五月', '六月', '七月', '八月', '九月', '十月', '十一月', '十二月'];
    var formatStr = {
      "M+": date.getMonth() + 1,
      "d+": date.getDate(),
      "h+": date.getHours() > 12 ? date.getHours() - 12 : date.getHours(),
      "H+": date.getHours(),
      "m+": date.getMinutes(),
      "s+": date.getSeconds(),
      "q+": Math.floor((date.getMonth() + 3) / 3),
      "w": '0123456'.indexOf(date.getDay()),
      "t": date.getHours() < 12 ? 'am' : 'pm',
      "W": _weekName[date.getDay()],
      "L": _monthName[date.getMonth()] //non-standard
    };
    if (/(y+)/.test(format)) {
      format = format.replace(RegExp.$1, (date.getFullYear() + '').substr(4 - RegExp.$1.length));
    }
    for (var k in formatStr) {
      if (new RegExp('(' + k + ')').test(format))
        format = format.replace(RegExp.$1, RegExp.$1.length == 1 ? formatStr[k] : ('00' + formatStr[k]).substr(('' + formatStr[k]).length));
    }
    return format;
  },
  pxToRem(_s) {
    // 匹配:20px或: 20px不区分大小写
    var reg = /(\:|: )+(\d)+(px)/gi
    let newStr = _s.replace(reg, function(_x) {
      _x = _x.replace(/(\:|: )/, '').replace(/px/i, '')
      return ':' + parseFloat(_x) / 100 + 'rem'
    })
    return newStr
  }
}

export default vfilter



