import * as types from './mutation-types'
// 同步方法

const mutations = {
  [types.LOADING_SHOW](state, flag) {
    state.loadingShow = flag
  },
  [types.SET_WEIXINSTATE](state, str) {
    state.weixinState = str
  },
  [types.SET_USERDATE](state, obj) {
    state.userData = obj
  },
  [types.SET_USERINFO](state, obj) { // 存储个人中心账号信息
    state.userInfo = obj
  },
  [types.MODIFY_EMAIL](state, str) { // 修改个人中心邮箱信息
    state.userInfo.email = str
  },
  [types.MODIFY_USERNAME](state, str) { // 修改个人中心用户名信息
    state.userInfo.nickname = str
  },
  [types.MODIFY_TELPHONE](state, str) { // 修改个人中心手机号信息
    state.userInfo.mobile = str
  },
  [types.SET_ADDRESSDATA](state, obj) { // 设置当前收货地址
    state.addressData = obj
  },
  [types.UPDATE_DIRECTION](state,payload) {
    state.direction = payload.direction
  }
}

export default mutations



